<template>
  <div class="largelist-demo">
    <large-list
      :showTitle="false"
      :showScroller="false"
      :titleHeight="titleHeight"
      :contentWidth="contentWidth"
      :contentHeight="contentHeight"
      :theme="theme"
      :fieldsConfig="fieldsConfig"
      @right-click="rightclick"
      @dbl-click="dblclick"
      @left-click="leftclick"
      @fieldsConfigChange="fieldsConfigChange"
    >
      <!-- <template slot-scope="slotScopes">
        <span
          v-for="(e, i) in fields"
          :key="i"
          :style="{
            height: slotScopes.itemHeight/2 + 'px',
            lineHeight: slotScopes.itemHeight/2 + 'px',
            backgroundColor:
            slotScopes.focusedObj === slotScopes.data.Obj ? theme.tableContentActiveBgColor:
            theme.tableContentBgColor}"
        >{{slotScopes.data[e] || '--'}}</span>
      </template> -->
    </large-list>
  </div>
</template>

<script>
import { theme, fieldsConfig } from "../utils/fieldsConfig.js";
export default {
  name: "largelist-demo",
  data() {
    let {clientHeight, clientWidth} = document.documentElement;
    return {
      theme,
      fieldsConfig,
      titleHeight: 40,
      contentHeight: clientHeight - 40,
      contentWidth: clientWidth
    };
  },
  created() {
    this.contentHeight =
      document.documentElement.clientHeight - this.titleHeight;
    this.resizeFunc = this.$Utils.debounce(() => {
      this.contentHeight =
        document.documentElement.clientHeight - this.titleHeight;
      this.contentWidth = document.documentElement.clientWidth;
    }, 300);
    window.addEventListener("resize", this.resizeFunc);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resizeFunc);
  },
  methods: {
    rightclick(obj) {
      console.log("right-click", obj);
    },
    dblclick(obj) {
      console.log("dbl-click", obj);
    },
    leftclick(obj) {
      console.log("left-click", obj);
    },
    fieldsConfigChange(new_fieldsConfig) {
      this.fieldsConfig = new_fieldsConfig
    }
  }
};
</script>
<style>
.largelist-demo {
  width: 100%;
  height: 100%;
}
</style>

